import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelBody, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  ShowModalTukang,
  showModal,
  getDataTukang,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import FormModalTukang from "./FormModalTukang.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";

const maptostate = (state) => {
  return {
    DataTukang: state.datamaster.getDataTukang,
    hideModal: state.datamaster.modalDialog,
  };
};
const hapusDataTukang = (params,id, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Tukang " +
      params +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("tukang/delete/by-kode-tukang/" + id, {
        data: {
          kode_tukang: params,
        },
      }).then(() => {
        NotifSucces("Data Berhasil Di Hapus").then(() =>
          dispatch(getDataTukang())
        );
      }).catch((err) => {
        ToastNotification("error", err.response.data)
      })
    }
  });
};
class MasterTukang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      modalDialog: false,
      columns: [
        {
          dataField: "kode_tukang",
          text: "Kode Tukang",
          sort: true,
        },
        {
          dataField: "nama_tukang",
          text: "Nama Tukang",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              _id: row._id,
              kode_sales: row.kode_tukang,
              nama_tukang: row.nama_tukang,
            };
             
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditTukang(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataTukang(row.kode_tukang,row._id, this.props.dispatch)
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
      // dataTukang: [
      //   {
      //     kode_tukang: "SAM",
      //     nama_tukang: "Samsul Arifin",
      //   },
      // ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentDidMount() {
    this.props.dispatch(getDataTukang());
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_edit = {
      nama_tukang: data.nama_tukang,
    };
    let data_post = {
      kode_tukang: data.kode_tukang,
      nama_tukang: data.nama_tukang,
    };
    this.state.isEdit
      ? putData("tukang/edit/by-kode-tukang/" + data._id, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getDataTukang());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalTukang"));
          })
        .catch((error) => {
          ToastNotification("info", "Terjadi Kesalahan Saat Mengirim Data")
          this.setState({ isLoading: false });
          })
      : postData("tukang/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Simpan");
          })
          .then(() => {
            this.props.dispatch(getDataTukang());
          })
          .then(() => {
            this.props.dispatch(reset("ModalTukang"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "tukang/reactive/by-kode-tukang/",
              data_post,
              this.props.dispatch,
              getDataTukang(),
              hideModal(),
              "ModalTukang"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }
  formEditTukang(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalTukang(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  ShowModalTukang() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalTukang(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Data Master</Link>
          </li>
          <li className="breadcrumb-item active">Master Tukang</li>
        </ol>
        <h1 className="page-header">Master Tukang </h1>
        <Panel>
          <PanelHeader>Master Tukang</PanelHeader>
          <br />

          {/* Master Tukang */}
          <PanelBody>
            <Tabel
              keyField="kode_tukang"
              tambahData={true}
              handleClick={() => this.ShowModalTukang()}
              exportCsv={true}
              data={this.props.DataTukang}
              columns={this.state.columns}
              empty={this.props.DataTukang}
              textEmpty="Data Tukang Kosong"
            />
          </PanelBody>
          <br />
          {/* End Master Tukang */}
          <ModalGlobal
            size="P"
            title={
              this.state.isEdit ? "Edit Data Tukang" : "Tambah Data Tukang"
            }
            content={
              <FormModalTukang
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* <Modal  title={ this.state.isEdit ? 'Edit Data Tukang' : 'Tambah Data Tukang' } content={ <FormModalKategori isEdit={this.state.isEdit} /> } isOpen={ this.props.hideModal} onSubmit={ (data)=>this.handleSubmit(data) } /> */}

          {/* End Tambah Master Tukang  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterTukang);
